let mainLoginButton = document.getElementById('mainLoginButton')


const mainC = new Controller(undefined,(): Promise<string> => {
    const p = new Promise<string>((resolve, reject) => {
        
        let coll:any=$('#collapseExample')
        coll.collapse('show')
        
        mainLoginButton?.addEventListener('click', () => {
            mainNumberInput.setAttribute('readonly', 'true')
            mainCodeInput.removeAttribute('readonly')
            mainPasswordInput.removeAttribute('readonly')
            return resolve((<HTMLInputElement>document.getElementById('mainNumber')).value)
        })
    })
    
    console.log(p)
    return p
},
    (): Promise<string> => {
        const p = new Promise<string>((resolve, reject) => {
            mainLoginButton?.addEventListener('click', () => {
                mainCodeInput.setAttribute('readonly', 'true')
                return resolve((<HTMLInputElement>document.getElementById('mainCode')).value)
            })
        })

        console.log(p)
        return p
    },
    (): Promise<string> => {
        const p = new Promise<string>((resolve, reject) => {
            mainLoginButton?.addEventListener('click', () => {
                mainPasswordInput.setAttribute('readonly', 'true')
                return resolve((<HTMLInputElement>document.getElementById('mainPassword')).value)
            })
        })
        console.log(p)
        return p
    })
    mainC.init()

